//      var account = nebulas.Account.fromAddress($this.wallet_key);
//      account.setPrivateKey('');
//
//      var gTx = new nebulas.Transaction($this.chainId,
//        account,
//        $this.contract_address, value,parseInt(nonce),parseInt(gas_price),parseInt(gas_limit),contract);
//
