//        .then(function (resp) {
//          console.log('resp:',resp);
//          $this.checkBalance();
//        }).catch(function (err) {
//        console.log("error:" + err.message)
//      })

      var serialNumber
      serialNumber = this.nebPay.call($this.contract_address, value, callFunction, callArgs, {    //nebpaycall,
